<template>
  <div class="card">
    <div class="card-header">
      Exam07ComputedProperties
    </div>
    <div class="card-body">
      <p>name: {{name}}</p>
      <p>ssn1: {{ssn1}}</p>
      <p>ssn2: {{ssn2}}</p>
      <p>ssn: {{ssn1}}-{{ssn2.charAt(0) + "******"}}</p>
      <p>ssn: {{getSsn()}}</p>
      <p>ssn: {{computedSsn}}</p>
      <hr/>
      <button class="btn btn-info btn-sm mr-2" @click="changeName">name 속성 변경</button>
      <button class="btn btn-info btn-sm mr-2" @click="changeSsn2">ssn2 속성 변경</button>
    </div>
  </div>   
</template>

<script setup>
import { ref, computed } from "vue";

//반응형 속성 선언
const name = ref("홍길동");
const ssn1 = ref("880515")
const ssn2 = ref("1110345")

const computedSsn = computed(() => {
  console.log("computed() callback 실행");
  const ssn = ssn1.value + "-" + ssn2.value.charAt(0) + " ******"; 
  return ssn;
})

//메소드 선언
function getSsn() {
  console.log("getSsn() 실행");
  const ssn = ssn1.value + "-" + ssn2.value.charAt(0) + " ******"; //.value를 붙이는 이유는 ref 속성의 값은 값 그 자체에 값이 들어있는 것이 아니기 떄문에 .value로 값을 가져와야 한다
  return ssn;
} //ssn과 상관없는 name의 값이 변경이 되어도 실행되는 문제가 있다

function changeName() {
  name.value += "동";
}

function changeSsn2() {
  ssn2.value +="2";
}
</script>

